* {
  padding: 0;
  margin: 0;
}

/* 
  GENERAL
*/
@import url("https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto&display=swap");

body {
  font-family: 'Open Sans' ,sans-serif;
  background-color: #EFEFEF;
}

button:focus {
  outline: none !important;
}

.row {
  margin: 0 0;
}

/* 
  BACKGROUND
*/
.bg-img {
  background-image: url(./assets/img/background.jpg);
  height: 100vh;
}

.bg-profile {
  background-color: #EFEFEF !important;
}

.bg-content {
  background-color: #fff;
  height: 100vh;
  margin-top: 50px;
}

@media (max-width: 768px) {
  .bg-content {
    background-color: #fff;
    height: 100vh;
    margin-top: 0;
  }
  .display-name {
    margin-top: 2vh !important;
    font-family: 'Roboto', sans-serif;
  }
}

/* 
  PROFILE SECTION
*/
.display-name {
  margin-top: 15vh;
  font-family: 'Roboto', sans-serif;
}

/* 
  SIGNIN/SIGNOUT PANEL
*/
.panel {
  margin-top: 30vh;
}

/* 
  CARD
*/
.card {
  border-radius: 0;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12);
  font-family: 'Roboto', sans-serif;
  width: auto !important;
}

.card-title {
  font-size: 20px;
}

.card-text {
  font-size: 16px;
}

.card .verify-btn {
  background: #3f51b5;
  border-radius: 2px;
  color: #fff;
  font-size: smaller;
  font-weight: 500;    
  display: inline-block;
  height: 34px;
  margin-left: 8px;
  min-width: 88px;
  border: none;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12);
}

.card .back-btn {
  background: #fff;
  border-radius: 2px;
  color: #3f51b5;
  font-size: smaller;
  font-weight: 500;    
  display: inline-block;
  height: 34px;
  min-width: 88px;
  border: none;
}

.card .back-btn:hover {
  background: #EFEFEF;
  transition: ease;
}